.react-datepicker {
	font-size: 16px;
	background-color: transparent;
	border: none;
	display: block;
	color: inherit;
	font-family: SourceSansPro-SemiBold;
	width: 280px;
	height: 310px;
}

.react-datepicker__current-month{
 height: 38px;
}

.react-datepicker__day, .react-datepicker__day-name{
  margin: 0 !important;
  width: calc(99%/7) !important;
	color: white;
	line-height: 0px;
	border: 2px solid transparent;
  padding-top: 17px;
  padding-bottom: 17px;
	-webkit-transition: all 0.1s;
	-o-transition: all 0.1s;
	transition: all 0.1s;
}



.react-datepicker__header {
	background-color: transparent;
	border: none;
	margin: 4px;
    font-family: SourceSansPro-Bold;
}



.react-datepicker__current-month {
	color: white;
	font-size: 16px;
	height: 22px;
}


.react-datepicker__navigation {
    line-height: 29px;
    text-align: center;
    cursor: pointer;
    position: absolute;
    top: 11px;
    width: 0;
    border: 8px solid transparent;
	}

a.react-datepicker__navigation.react-datepicker__navigation--previous {
		border-right-color: white;
		left: 7px;
	}
a.react-datepicker__navigation.react-datepicker__navigation--next {
	border-left-color: white;
	right: 7px;
}




.react-datepicker__day-name {
	color: white;
	margin: 10px;
	margin-bottom: 0px;
}

.react-datepicker__day--selected,
.react-datepicker__day:hover {
	background-color: white;
	color: inherit;
}




.calendar-container {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-align: center;
	    align-items: center;
	-ms-flex-direction: column;
	    flex-direction: column;
	height: inherit;
}

.react-datepicker__day--disabled:hover,
.react-datepicker__day--disabled {
	background-color: transparent;
	color: rgba(255,255,255,0.5);
}


.dark .react-datepicker__current-month,
.dark .react-datepicker__day, 
.dark .react-datepicker__day-name{
	color: #4A4A4A;
}

.dark a.react-datepicker__navigation.react-datepicker__navigation--next{
	border-left-color: #4A4A4A;
}

.dark a.react-datepicker__navigation.react-datepicker__navigation--previous{
	border-right-color: #4A4A4A;
}
.dark .react-datepicker__day--disabled:hover,
.dark .react-datepicker__day--disabled {
	background-color: transparent;
	color: lightgrey;
}
.dark .react-datepicker__day--selected,
.dark .react-datepicker__day:hover {
	background-color: rgb(37, 244, 210);
	color: white;
}


.react-datepicker__month-container{
	float: none;
}


